#profile{background-image:-webkit-linear-gradient(top, #224365, #0a1b30);background-image:linear-gradient(to bottom, #224365, #0a1b30);padding-top:20px}#profile #user-profile{display:-webkit-box;display:-ms-flexbox;display:flex;padding:10px 15px}#profile #user-profile #avatar{background-position:center center;background-size:cover;height:100px;width:100px;border-radius:6px}#profile #user-profile #user-info{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;margin-left:15px}#profile #user-profile #user-info #user-info-upper h1{font-family:GothamPro-Medium;font-size:18px;color:#ffffff;margin:2px 0 12px;min-height:19px}#profile #user-profile #user-info #user-info-upper h2{font-family:GothamPro;font-size:14px;color:#ffffff;margin:0;min-height:15px}#profile #user-bio{opacity:.4;font-family:GothamPro;font-size:16px;text-align:center;color:#ffffff;margin:20px 0;padding:0 10px;min-height:22px;line-height:20px}#profile #user-stats{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around;-webkit-box-align:center;-ms-flex-align:center;align-items:center}#profile #user-stats .stats-title{font-family:GothamPro-Bold;font-size:22px;text-align:center;color:#ffffff;min-height:31px}#profile #user-stats .stats-description{font-family:GothamPro;font-size:13px;letter-spacing:.6px;text-align:center;color:#aeb6c0}#profile #user-stats .stats-divider{width:2px;height:20px;background-color:rgba(255,255,255,0.2)}#profile #user-stats .stats-divider.space-holder{background-color:rgba(0,0,0,0)}#profile #view-repos{text-align:center;padding:35px 0}


