#search-input{width:100%;position:relative}#search-input input{width:100%;height:40px;border-radius:8px;background-color:#ffffff;font-family:GothamPro;font-size:14px;border:0;color:#8f9aa8;padding-left:15px;padding-right:55px;-webkit-appearance:none}#search-input input:focus{outline:0}#search-input button{height:40px;border-radius:0 8px 8px 0;background-image:-webkit-linear-gradient(top, #a5de41, #55b130);background-image:linear-gradient(to bottom, #a5de41, #55b130);box-shadow:inset 0 .5px .5px 0 #e2ffce;border:none;position:absolute;right:0;font-family:GothamPro-Bold;font-size:14px;letter-spacing:.5px;text-align:center;color:#ffffff;padding:0 20px}#search-input button.icon{padding:0;width:44px;font-size:20px}#search-input button:focus{outline:0}


