#nav-menu{background-color:#3d536d;min-height:100%;width:100%;height:100vh;opacity:1;-webkit-transform:translate3d(-150px, 0, 0);transform:translate3d(-150px, 0, 0);-webkit-transition:opacity .4s,-webkit-transform .3s cubic-bezier(.7, 0, .25, 1);transition:opacity .4s,-webkit-transform .3s cubic-bezier(.7, 0, .25, 1);transition:transform .3s cubic-bezier(.7, 0, .25, 1),opacity .4s;transition:transform .3s cubic-bezier(.7, 0, .25, 1),opacity .4s,-webkit-transform .3s cubic-bezier(.7, 0, .25, 1)}#nav-menu #search-bar{position:relative;padding:15px;-webkit-transition:background-color .3s;transition:background-color .3s;z-index:1}#nav-menu #search-bar.dark-bg{background-color:#2c4052}#nav-menu #search-bar #search-input{width:84%}#nav-menu #search-bar #cancel-button{position:absolute;top:0;right:0;height:70px;width:16%;text-align:left;line-height:70px;color:#fff;opacity:0;-webkit-transition:opacity .3s .2s;transition:opacity .3s .2s;font-size:14px}#nav-menu #user-list{position:absolute;left:0;top:70px;right:0;bottom:0;overflow-y:scroll;overflow-x:hidden;-webkit-overflow-scrolling:touch}#nav-menu #user-list .user-item{display:-webkit-box;display:-ms-flexbox;display:flex;padding:12px 15px;will-change:transform}#nav-menu #user-list .user-item .user-avatar{width:40px;height:40px;border-radius:5px;background-position:center center;background-size:cover;margin-right:20px;background-color:rgba(255,255,255,0.5)}#nav-menu #user-list .user-item .fullname{font-family:GothamPro-Medium;font-size:16px;color:#ffffff;margin-bottom:9px}#nav-menu #user-list .user-item .username{font-family:GothamPro;font-size:12px;color:#8f9aa8}#nav-menu #user-list #loading{text-align:center}@-webkit-keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.loading{border-radius:50%;width:24px;height:24px;border:.25rem solid rgba(255,255,255,0.2);border-top-color:white;-webkit-animation:spin 1s infinite linear;animation:spin 1s infinite linear;margin:0 auto}


