#loading-block{height:3px;width:100%;background-color:#46a42f;-webkit-transform:scaleX(0);transform:scaleX(0);z-index:3;position:absolute;-webkit-transform-origin:0 0;transform-origin:0 0}#loading-block.failed{background-color:#f44336;-webkit-transform:scaleX(1) !important;transform:scaleX(1) !important;-webkit-transition:-webkit-transform .3s !important;transition:-webkit-transform .3s !important;transition:transform .3s !important;transition:transform .3s, -webkit-transform .3s !important}#loading-block.one{-webkit-transform:scaleX(.2);transform:scaleX(.2);-webkit-transition:-webkit-transform .5s;transition:-webkit-transform .5s;transition:transform .5s;transition:transform .5s, -webkit-transform .5s}#loading-block.two{-webkit-transform:scaleX(.8);transform:scaleX(.8);-webkit-transition:-webkit-transform 20s cubic-bezier(0, 1.17, 1, 1);transition:-webkit-transform 20s cubic-bezier(0, 1.17, 1, 1);transition:transform 20s cubic-bezier(0, 1.17, 1, 1);transition:transform 20s cubic-bezier(0, 1.17, 1, 1), -webkit-transform 20s cubic-bezier(0, 1.17, 1, 1)}#loading-block.three{-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transition:-webkit-transform .3s;transition:-webkit-transform .3s;transition:transform .3s;transition:transform .3s, -webkit-transform .3s}


