.repo-content{padding:15px;background-color:#fff}.repo-content .repo-pretitle{font-family:GothamPro;font-size:13px;letter-spacing:-0.1px;color:#5c6877}.repo-content .repo-title{font-family:GothamPro-Medium;font-size:18px;color:#5c6877;letter-spacing:-0.2px;line-height:1.3;margin-bottom:7px}.repo-content .repo-desc{font-family:GothamPro;font-size:14px;line-height:1.5;color:#77818e;margin-top:5px;word-wrap:break-word}.repo-content .repo-date{font-family:GothamPro;font-size:13px;color:#aeb6c0;margin-top:5px}.repo-content .repo-info{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;margin-top:15px}.repo-content .repo-language{font-family:GothamPro-Bold;font-size:14px;color:#8f9aa8;letter-spacing:.3px}.repo-content .repo-stats{font-family:GothamPro;font-size:14px;color:#77818e;letter-spacing:.3px}.repo-content .repo-stats .fa{margin:0 0 0 10px}


